/*
 *  Default theme - Owl Carousel CSS File
 */


.owl-nav {
  margin-top: 10px;

  text-align: center;

  -webkit-tap-highlight-color: transparent;
  [class*='owl-'] {
    font-size: 14px;

    display: inline-block;

    margin: 5px;
    padding: 4px 7px;

    cursor: pointer;

    color: #fff;
    border-radius: 3px;
    background: #d6d6d6;
    &:hover {
      text-decoration: none;

      color: #fff;
      background: #869791;
    }
  }
  .disabled {
    cursor: default;

    opacity: .5;
  }
  &.disabled + .owl-dots {
    margin-top: 10px;
  }
}
.owl-dots {
  text-align: center;

  -webkit-tap-highlight-color: transparent;
  .owl-dot {
    display: inline-block;
    zoom: 1;

    *display: inline;
    span {
      display: block;

      width: 10px;
      height: 10px;
      margin: 5px 7px;

      transition: opacity 200ms ease;

      border-radius: 30px;
      background: #d6d6d6;

      -webkit-backface-visibility: visible;
    }
    &.active span,
    &:hover span {
      background: #869791;
    }
  }
}
