body {
  font-family: "Segoe UI", sans-serif;
  font-size: 18px;
  font-weight: 100;
  line-height: 1.2;
  min-width: 320px;
  transition: background-color 750ms ease, color 105ms ease, opacity 45ms ease;
  color: #202020;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Segoe UI", sans-serif;
  font-size: 18px;
  line-height: 1.2;
  margin: 0 0 0.6em;
}

em {
  font-style: italic;
}

p,
pre {
  margin: 0 0 0.6em;
}

a {
  transition: background-color 750ms ease, color 105ms ease, opacity 45ms ease;
  text-decoration: none;
  color: #477dca;
}

a:active, a:focus, a:hover {
  color: #2c5999;
}

a:active, a:focus {
  color: #82a7db;
  outline: none;
}

hr {
  margin: 1.2em 0;
  border-top: none;
  border-right: none;
  border-bottom: 1px solid #d2d2d2;
  border-left: none;
}

img,
picture {
  max-width: 100%;
  margin: 0;
}

.wrapper {
  position: relative;
  width: 100%;
  height: auto !important;
  height: 100%;
  min-height: 100%;
  margin: 0 auto;
}

.container {
  position: relative;
}

.flex-row {
  display: block;
  display: flex;
  align-items: stretch;
  flex-flow: row wrap;
}

header {
  height: 130px;
}

header .present-line {
  font-size: 16px;
  font-weight: 300;
  position: relative;
  width: 100%;
  height: 40px;
  padding-top: 8px;
  padding-bottom: 8px;
  text-align: center;
  color: #36a6c9;
  background-color: #ecf1f7;
}

header .header-line {
  line-height: 70px;
  height: 90px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all .3s;
  vertical-align: middle;
  background: #fff;
}

header .header-line .container {
  height: 100%;
}

header .header-line .logo {
  position: relative;
  display: inline-block;
  float: left;
  width: 150px;
  vertical-align: middle;
  align-items: center;
}

header .header-line .logo img {
  display: inline-block;
  width: auto;
  max-width: 100%;
  height: 67px;
}

header .header-line .slogan {
  position: relative;
  display: inline-block;
  float: left;
  width: calc(100% - 676px);
  text-align: center;
}

header .header-line .slogan p {
  margin: auto;
}

header .header-line .slogan b,
header .header-line .slogan strong {
  display: inline;
  color: #477dca;
}

header .header-line .lang-nav {
  position: relative;
  display: inline-block;
  float: left;
  box-sizing: border-box;
  width: 110px;
  text-align: left;
}

header .header-line .lang-nav:after {
  position: absolute;
  top: 50%;
  right: 0;
  left: auto;
  display: inline-block;
  width: 0;
  height: 34px;
  margin-top: -17px;
  content: '';
  border-left: 1px dashed #d2d2d2;
}

header .header-line .lang-nav ul {
  line-height: 1.2;
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: left;
}

header .header-line .lang-nav ul:before {
  position: absolute;
  top: 50%;
  left: 70px;
  display: inline-block;
  width: 10px;
  margin-top: -5px;
  content: '\25bc';
  vertical-align: middle;
  color: #8f8f8f;
}

header .header-line .lang-nav ul li {
  position: relative;
  display: inline-block;
  float: none;
  box-sizing: border-box;
  width: 34px;
  height: 34px;
  margin: 0;
  padding: 0;
  border: 1px solid #36a6c9;
  border-radius: 100%;
}

header .header-line .lang-nav ul li a {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  border-radius: 100%;
}

header .header-line .lang-nav ul li a img {
  display: inline-block;
  box-sizing: border-box;
  width: auto;
  min-width: 100%;
  height: 100%;
  border-radius: 100%;
}

header .header-line .lang-nav ul li a span {
  line-height: 34px;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 100%;
  display: inline-block;
  overflow: hidden;
  width: 32px;
  height: 34px;
  vertical-align: middle;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  color: #202020;
}

header .header-line .lang-nav ul li .sub-menu {
  position: absolute;
  z-index: 997;
  top: 100%;
  right: -30px;
  left: -10px;
  overflow: hidden;
  width: auto;
  max-height: 0;
  padding: 0 10px;
  transition: all .3s linear;
  opacity: 0;
}

header .header-line .lang-nav ul li .sub-menu .current-lang {
  display: none;
}

header .header-line .lang-nav ul li .sub-menu li {
  margin-bottom: 5px;
}

header .header-line .lang-nav ul li .sub-menu span {
  display: none;
}

header .header-line .lang-nav ul li .sub-menu:before, header .header-line .lang-nav ul li .sub-menu:after {
  display: none;
}

header .header-line .lang-nav ul li:hover .sub-menu {
  max-height: 999px;
  padding: 10px;
  opacity: 1;
}

header .header-line .head-contacts {
  line-height: 1.2;
  position: relative;
  display: inline-block;
  float: left;
  width: 340px;
  height: 100%;
  margin: auto;
  padding-top: 5px;
  padding-right: 20px;
  padding-left: 20px;
  text-align: right;
}

header .header-line .head-contacts .main-phone {
  font-size: 25px;
  position: relative;
  display: inline-block;
  float: right;
  margin-bottom: 8px;
  padding: 0;
  text-align: right;
  color: #202020;
}

header .header-line .head-contacts .main-phone strong,
header .header-line .head-contacts .main-phone b {
  color: #11485d;
}

header .header-line .head-contacts .main-phone:before {
  position: absolute;
  top: 0;
  left: -30px;
  display: inline-block;
  width: 30px;
  height: 30px;
  content: '';
  pointer-events: auto;
  background: url("../img/icon-head-phone.png") no-repeat center center;
  background-size: contain;
}

header .header-line .head-contacts .contacts {
  display: inline-block;
  width: 100%;
}

header .header-line .head-contacts .contacts .tel_wrap {
  position: relative;
  display: inline-block;
  float: right;
  width: 60%;
  max-width: 180px;
  margin: 0;
  padding: 3px 0 0  45px;
}

header .header-line .head-contacts .contacts .tel_wrap .head-viber {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  display: inline-block;
  width: 25px;
  height: 25px;
  background: url("../img/icon-viber.png") no-repeat center center;
  background-size: contain;
}

header .header-line .head-contacts .contacts .tel_wrap .head-watsup {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 18px;
  display: inline-block;
  width: 27px;
  height: 27px;
  background: url("../img/icon-whatsapp.png") no-repeat center center;
  background-size: contain;
}

header .header-line .head-contacts .contacts .tel_wrap .head-phone {
  font-size: 16px;
  width: 100%;
  text-align: left;
  color: #202020;
}

header .header-line .head-contacts .contacts .tel_wrap .head-phone b {
  font-weight: 200;
  color: #11485d;
}

header .header-line .head-contacts .contacts .head-skype {
  font-size: 16px;
  position: relative;
  display: inline-block;
  float: left;
  width: 40%;
  margin: 0;
  padding: 3px 0 0 35px;
  text-align: left;
  text-decoration: underline;
}

header .header-line .head-contacts .contacts .head-skype:hover {
  text-decoration: none;
}

header .header-line .head-contacts .contacts .head-skype:before {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 25px;
  height: 25px;
  content: '';
  pointer-events: auto;
  background: url("../img/icon-skype.png") no-repeat center center;
  background-size: contain;
}

header .header-line.stick {
  position: fixed;
  z-index: 998;
  top: 0;
  right: 0;
  left: 0;
  height: 70px;
  padding-top: 0;
  padding-bottom: 0;
  background: #fff;
}

header .header-line.stick .logo img {
  height: 50px;
}

.head-cart {
  position: relative;
  display: inline-block;
  float: left;
  width: 75px;
  height: 100%;
  text-align: right;
}

.head-cart .my-cart-icon {
  position: absolute;
  top: 50%;
  right: 0;
  display: inline-block;
  width: 54px;
  height: 46px;
  margin: -23px auto auto auto;
  cursor: pointer;
  background: url("../img/icon-minicart.png") no-repeat center;
  background-size: contain;
}

.head-cart .my-cart-icon .my-cart-badge {
  line-height: 25px;
  position: absolute;
  right: 0;
  bottom: -7px;
  display: none;
  width: 25px;
  height: 25px;
  padding-bottom: 5px;
  text-align: center;
  vertical-align: middle;
  color: #fff;
  border-radius: 100%;
  background: #a4d300;
}

.head-cart .my-cart-icon .my-cart-badge.show {
  display: inline-block;
}

.head-cart:before {
  position: absolute;
  top: 50%;
  right: auto;
  left: 0;
  display: inline-block;
  width: 0;
  height: 34px;
  margin-top: -17px;
  content: '';
  border-left: 1px dashed #d2d2d2;
}

.best-seler {
  line-height: 1;
  position: absolute;
  top: 20px;
  left: 90px;
  display: inline-block;
  width: 78px;
  height: 78px;
  margin: -39px;
  padding-top: 10px;
  transform: rotate(-45deg);
  text-align: center;
  color: #cb0000;
  border-radius: 100%;
  background: #fff000;
}

.best-seler b,
.best-seler strong {
  font-size: 25px;
}

